<template>
    <div :style="{ 'background-image': `url(${headImg})` }" class="error">
        <div class="error-inner">
            <el-image :src="errorImg" class="error-img"></el-image>
            <p class="error-status">{{ status }}</p>
            <p>{{ msg }}</p>
        </div>
    </div>
</template>

<script>
import alertImg from '@/assets/images/img-error.png'
import missingImg from '@/assets/images/img-missing.png'
import headImg from '@/assets/images/weixin-head.svg'

export default {
    data() {
        return {
            headImg,
            status: this.$route.query.status,
            msg: this.$route.query.msg
        }
    },
    computed: {
        errorImg() {
            return this.status == '404' ? missingImg : alertImg
        }
    }
}
</script>

